<template>
	<h2 hidden v-if=" state.stateStoreTransmeter=='1'">{{this.$store.commit('setUserIsLogged')}}</h2>
	<h2 hidden v-else>{{this.$store.commit('setUserIsNotLogged')}}</h2>
	<div class="container">
		<div class="screen">
			<div class="screen__content">
				<form class="login" action="" id="frm" ref="frm">
					<h1 style="color: #7875B5; ">로그인</h1>
					<div class="login__field">
						<input type="text" class="login__input" aria-describedby="emailHelp" ref="email"
							v-model="state.email" placeholder="이메일" required autofocus>
					</div>
					<div class="login__field">
						<input type="password" class="login__input" placeholder="비밀번호" ref="pw" v-model="state.pw">
					</div>
					<button class="button login__submit" @click.prevent="loginHandler">
						<span class="button__text">지금 로그인 하기
							&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;>></span>
					</button>
					<h5 style="margin-top:1em">
						<RouterLink to="/signup" style="text-decoration:none; color:#7875B5;">회원 가입</RouterLink>
					</h5>
					<h6>
						<RouterLink to="/forgot" style="text-decoration:none; color:#30353d;">비밀번호 찾기
						</RouterLink>
					</h6>
				</form>
			</div>
			<div class="screen__background">
				<span class="screen__background__shape screen__background__shape4"></span>
				<span class="screen__background__shape screen__background__shape3"></span>
				<span class="screen__background__shape screen__background__shape2"></span>
				<span class="screen__background__shape screen__background__shape1"></span>
			</div>
		</div>
	</div>
</template>

<script charset='utf-8'>
import { reactive, ref, computed } from 'vue';
import axios from 'axios';
import { useRouter } from 'vue-router'

export default {
	computed: {
		complogIn() {
			console.log('I do  computed logIn')
			return this.$store.commit('setUserIsLogged')

		},
		complogOut() {
			console.log('I do computed logOut')
			return this.$store.commit('setUserIsNotLogged')
		},
	},
	setup() {

		const logOut = () => {
			console.log("you are logged out")
			state.stateStoreTransmeter = 0
		}
		const storeTransmeter = ref('');

		const router = useRouter();
		const state = reactive({ 
			email: 'nolaboda37@ds.org',
		 pw: '1', 
		 stateStoreTransmeter: 'default' })
		//TODO:fix db cannot have multiple same email
		const email = ref('');
		const pw = ref('')
		const logIn = computed(() => {
			return this.$store.commit('setUserIsLogged')
		})
		const loginHandler = async () => {
			if (state.email === '') {
				alert('Check Email'); email.value.focus(); return false;
			} else if (state.pw === '') {
				alert('Check Password'); pw.value.focus(); return false;
			}
			const url = "http://43.201.60.125/members/login"
			const headers = { "Content-Type": "application/json; charset=utf-8" }
			const body = { email: state.email, pw: state.pw }
			console.log("body: " + body);
			console.log("email: " + email);
			await axios.post(url, body, { headers }).then(function (res) {
				sessionStorage.setItem("TOKEN", res.data.token)
				sessionStorage.setItem("email", res.data.email)
				sessionStorage.setItem("guest", false)

				alert('로그인되었습니다.')
				console.log(res.data.token)
				console.log(res.data.email)
				console.log(sessionStorage.getItem("TOKEN"))
				console.log(sessionStorage.getItem("email"))
				state.stateStoreTransmeter = 1
				router.push({ name: "commandBoard" });

			}).catch(function (error) {
				console.log('Error', error.message);
				alert("Login failed correct your identifiants or go to \"I forgot my password\"")
				state.stateStoreTransmeter = 0
			})
		}
		return { state, email, pw, loginHandler, logIn, logOut }
	},
	data() {
		return {}
	}
}
</script>

<style scoped>
* {
	box-sizing: border-box;
	margin: 0, auto;
	padding: 0;
	font-family: Raleway, sans-serif;
}

body {
	background: linear-gradient(90deg, #C7C5F4, #776BCC);
}

.container {
	display: flex;
	align-items: center;
	justify-content: center;
	min-height: 100vh;
}

.screen {
	background: linear-gradient(90deg, #5D54A4, #7C78B8);
	position: relative;
	height: 600px;
	width: 360px;
	box-shadow: 0px 0px 24px #5C5696;
}

.screen__content {
	z-index: 1;
	position: relative;
	height: 100%;
}

.screen__background {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	z-index: 0;
	-webkit-clip-path: inset(0 0 0 0);
	clip-path: inset(0 0 0 0);
}

.screen__background__shape {
	transform: rotate(45deg);
	position: absolute;
}

.screen__background__shape1 {
	height: 520px;
	width: 520px;
	background: #FFF;
	top: -50px;
	right: 120px;
	border-radius: 0 72px 0 0;
}

.screen__background__shape2 {
	height: 220px;
	width: 220px;
	background: #6C63AC;
	top: -172px;
	right: 0;
	border-radius: 32px;
}

.screen__background__shape3 {
	height: 540px;
	width: 190px;
	background: linear-gradient(270deg, #5D54A4, #6A679E);
	top: -24px;
	right: 0;
	border-radius: 32px;
}

.screen__background__shape4 {
	height: 400px;
	width: 200px;
	background: #7E7BB9;
	top: 420px;
	right: 50px;
	border-radius: 60px;
}

.login {
	width: 320px;
	padding: 30px;
	padding-top: 100px;
}

.login__field {
	padding: 20px 0px;
	position: relative;
}

.login__input {
	border: none;
	border-bottom: 2px solid #D1D1D4;
	background: none;
	padding: 10px;
	padding-left: 24px;
	font-weight: 700;
	width: 75%;
	transition: .2s;
}

.login__input:active,
.login__input:focus,
.login__input:hover {
	outline: none;
	border-bottom-color: #6A679E;
}

.login__submit {
	background: #fff;
	font-size: 14px;
	margin-top: 30px;
	padding: 16px 20px;
	border-radius: 26px;
	border: 1px solid #D4D3E8;
	text-transform: uppercase;
	font-weight: 700;
	display: flex;
	align-items: center;
	width: 100%;
	color: #4C489D;
	box-shadow: 0px 2px 2px #5C5696;
	cursor: pointer;
	transition: .2s;
}

.login__submit:active,
.login__submit:focus,
.login__submit:hover {
	border-color: #6A679E;
	outline: none;
}
</style>
